import { useEffect, useState } from "react";
import { useFuel } from "./useFuel"; //  useFuel


export function useIsConnected() {
  const [fuel] = useFuel(); //   useFuel   fuel

  const [isConnected, setIsConnected] = useState(false);

  useEffect(() => {
    async function handleConnection() {
      if (fuel) {
        //  fuel   useFuel
        const isConnected = await fuel.isConnected();
        setIsConnected(isConnected);
      }
    }

    if (fuel) {
      //  fuel   useFuel
      handleConnection();
    }

    fuel?.on(fuel.events.connection, handleConnection); //  fuel   useFuel
    return () => {
      fuel?.off(fuel.events.connection, handleConnection); //  fuel   useFuel
    };
  }, [fuel]);

  return [isConnected];
}
